<script>
import DataService from "../../../services/DataService";
export default {
    props:{
        orderIdForConvertToProject:null
    },
    data: function () {
        return {
            conProjectName: '',
            conProjectTemplate: '',
            ProjectTemplateListJson: []
        }
    },
    created: function () {
        debugger;
        this.GetProjectTemplatelist();
       
    },
    methods: {
        GetProjectTemplatelist: function () {
            var vm = this;
            var url = "id=&moduleName=CRM&type=PROJECT";
            DataService.getDDLData(url).then(function (response) {
                var json = response.data;
                if (json != null) {
                    vm.ProjectTemplateListJson = json.DATA;
                }
            });
        },
        convertToProject: function () {
            var vm = this;
            vm.$refs.form.setFieldTouched('projectTemplate', true);
            vm.$refs.form.setFieldTouched('projectName', true);
            vm.$refs.form.validate().then((result) => {
                if (result.valid) {
                    var url = "name=" + vm.conProjectName + "&type=PROJECT";
                    DataService.CheckDuplicate(url).
                        then(function (response) {
                            var json = response.data;
                            if (json != null) {
                                if (json == 1) {
                                    vm.ShowAlert(vm.$t("ProjectNameAlreadyExist"), "warning", true, vm.$t("Alert"));
                                } else {
                                    var ConvertQuoteData = {
                                        orderId: vm.orderIdForConvertToProject,
                                        projectName: vm.conProjectName,
                                        projectTmplateId: vm.conProjectTemplate
                                    };
                                    var objectPostString = JSON.stringify(ConvertQuoteData);
                                    DataService.ConvertQuoteToProject(objectPostString).then(function (response) {
                                        if (response.data[0].Status == "Success") {
                                            vm.$emit('close');
                                            vm.ShowAlert(vm.$t('LeadConvertSuccess',[vm.$t('QuotationTitle'),vm.$t('Project')]), "success", true, vm.$t("Alert"));
                                        }
                                        else {
                                            vm.ShowAlert(vm.$t('LeadConvertError', [vm.$t('QuotationTitle'),vm.$t('Project')]), "failure", true, vm.$t("Alert"));
                                        }
                                    });
                                }
                            }
                        });
                }
            });
        }
    }
}
</script>
<template>
    
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('ConvertQuotation') }}
                            </h5>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times"
                                    aria-hidden="true"></em></button>
                        </div>
                        <div class="modal-body">
                            <Form ref="form">
                                <div class="d-flex flex-wrap">
                                    <div class="col-md-12 p-0">
                                        <div class="row">
                                            <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                                <div class="form-group">
                                                    <label>{{ $t('ProjectTemplate') }}:<span
                                                            class="text-danger">*</span></label>
                                                            <Field  name="projectTemplate" :rules="{'required':true}" 
                                                            label="project template"  v-model="conProjectTemplate"
                                                            v-slot="{errors,touched,invalid}">
                                                    <select v-bind:class="{'form-control': true,'is-invalid': touched && invalid}" name="projectTemplate"
                                                        >
                                                        <option value="">{{ $t('SelectTemplate') }}</option>
                                                        <option v-for="option in ProjectTemplateListJson" :key="option"
                                                            v-bind:value="option.value">{{ option.name }}</option>
                                                    </select>
                                                    <span   name="projectTemplate" class="invalid-feedback">{{errors[0]}}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                            <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                                <div class="form-group">
                                                    <label>{{ $t('ProjectName') }}:</label>
                                                    <Field  name="projectName" :rules="{'required':true}" 
                                                            label="project name" 
                                                            v-slot="{errors,touched,invalid}">
                                                    <input  type="text" name="projectName" id="projectName"
                                                        v-model="conProjectName" v-bind:class="{ 'form-control': true,'is-invalid': touched && invalid }"
                                                        :placeholder="$t('ProjectName')" />
                                                        <span   name="projectName" class="invalid-feedback">{{errors[0]}}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="row flex-row-reverse">
                                            <div class="col-lg-6 text-right">
                                                <a href="javascript:;" class="btn btn-success mr-2"
                                                    v-on:click="convertToProject()"><em class="fa fa-retweet pr-2"></em>
                                                    {{ $t('Convert') }}</a>
                                                <a href="javascript:;" class="btn btn-danger"
                                                    v-on:click="$emit('close')"><em class="fa fa-times pr-2"></em>
                                                    {{ $t('Cancel') }} </a>
                                            </div>
                                            <div class="col-lg-6">
                                                <div class="py-2"></div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
   
</template>