<script>
import Schema from "../../CRM/Quote/QuoteSchema";
import TgList from "../../../components/Common/TgList.vue"
import QuoteDataService from "../../../services/DataService";
import searchfilter from "../../../components/Common/Modal/search-filter.vue";
import viewlayout from "../../../components/Common/Modal/view-layout.vue";
import modalemail from "../../../components/Common/Modal/sendbulkemail.vue";
import modalfollowupslist from "../../../components/Common/Modal/modalfollowupslist.vue";
import  modalconvertQuoteToProject from '../../../components/Common/Modal/modalconvertQuoteToProject.vue';

export default {
     components: {
        TgList,
        viewlayout,
        modalemail,
        searchfilter,
        modalfollowupslist,
        modalconvertQuoteToProject                   
    },
    data(){
        return{
            LegendArray:[],
            isLoading: false,
            leftSearchFields: Schema.leftSearchFields,
            userid: null,
            usertype:null,           
            searchCondition: '',
            selectedQuoteType: '',
            ddlQuoteType: '',
            Status: '',
            txtquotetitle: '',
            txtquotenumber: '',
            quotationNumberId: '',
            Title: '',
            QuotationType: '',
            quotationDateFrom: '',
            quotationDateTo: '',
            IsAnySearch: false,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            listheaderbuttons: Schema.buttons,
            Headers: null,
            noRecord: false,
            listActions: Schema.listActions,
            TotalRecords: 0,
            StatusListForListing: [],            
            IsAnySearch: false,           
            souceID: 0,
            QuotationData: null,
            showAddEventModal: false,
            ShowManageView: false,
            showFilter: false,
            showAddEmailTemplate:false,
            appliedFilter:[],
            privilegeParams: null,
            orderIdForConvertToProject:null,
            showQuoteToProjectModal:false
        }
    },  
    created: function() {      
        this.userid = this.GetUserInfo.ID;
       this.usertype = this.GetUserInfo.UserType;
       this.privilegeParams = `controller=CRM&action=`;
        var VueObj = this;
        VueObj.getStatuslistForListing('CRM', 'en');
        VueObj.FetchData();
        VueObj.LegendArray = [
                {
                    class: 'bg-warning colorboxsquare',
                    text: (VueObj.$t('ListingLegend', ['Quotation']))
                }
            ]
    },
    updated: function () {       
         this.BindActionButtonEvent();
    },
    methods:{
        FilterData:function(pageNum,pageSize,sortBy,sortExp,searchcondition,filterItemData){
               this.pageNum=pageNum;
               this.pageSize=pageSize;
               this.sortBy=sortBy;
               this.sortExp=sortExp;
               this.searchCondition=searchcondition;
               if(typeof filterItemData!=null)
               this.appliedFilter.push(filterItemData);
             this.FetchData();
            },
        FetchData: function(downloadExceldata){
            const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchcondition);                        
            var VueObj = this;
            VueObj.isLoading = true;       
            if(downloadExceldata){
                params.pageSize = VueObj.TotalRecords;
            }else{ 
                params.pageSize = VueObj.PageSize;
            }                  
            
            params.pageNumber = VueObj.PageNumber;
            params.searchCondition = VueObj.searchCondition;
            if (VueObj.PageNumber) { params.pageNum = VueObj.PageNumber; }
            else { VueObj.PageNumber = params.pageNum = 1; }
            if (typeof VueObj.isPaged != 'undefined') { params.isPaged = VueObj.isPaged; }
            else { VueObj.isPaged = params.isPaged = 1; }
            params.isPaged = VueObj.isPaged;
            params.sortBy = VueObj.SortBy;
            params.sortExp = VueObj.SortExp;             
            var srchCondition = "";
            if (!VueObj.isBlank(this.searchCondition)) { srchCondition = srchCondition + VueObj.searchCondition; }
            if (!VueObj.isBlank(this.conditionForInstantSearch)) {srchCondition = srchCondition + ((!VueObj.isBlank(VueObj.searchCondition)) ? (" AND " + VueObj.conditionForInstantSearch) : VueObj.conditionForInstantSearch);}
            if (srchCondition != "") {
                params.searchCondition = srchCondition; 
                    if(VueObj.appliedFilter.length>0){
                    const removeFilterindex= VueObj.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'removeFilter';
                    });          
                    VueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                    const openFilterPopUpindex= VueObj.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'openFilterPopUp';
                    });
                    VueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';               
                }
            }
            else{
                if(vueObj.appliedFilter.length==0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
          }
            if(downloadExceldata){
                params.isPaged = 0;
                QuoteDataService.QuotesListing(params)
                    .then(function (response) {
                        VueObj.isLoading = false;
                        if (response.data != "" && response.data != null) {
                            VueObj.JSONToCSVConvertor(response.data, "Quotation-Detail", true);
                        }
                        else {
                            VueObj.ModelnoRecord = true;
                        }
                        
                    });
            }else{
                QuoteDataService.QuotesListing(params).then(function(response){
                    VueObj.isLoading = false;
                    if (response.data != null) {
                        VueObj.Headers = response.data.schema;
                        VueObj.Headers.forEach((column) => {
                            column.settings = null;
                            if(column.COLUMN_NAME == 'status_id'){
                                column.settings = {
                                    isInSlot : true
                                }
                            }
                            if(column.COLUMN_NAME == 'order_number'){
                                column.settings = {
                                    clickEvent: VueObj.clickEventOfColumn,
                                    formatter: VueObj.columnDataFormatter
                                }
                            }
                        });
                        if (response.data.data.length > 0) {
                            VueObj.QuotationData = response.data.data;
                            VueObj.QuotationData.forEach((row)=>{
                                row.isCheckBoxDisabled=false;
                                row.additionalClass='';
                                if(row.status_id=='Closed'){
                                    row.isCheckBoxDisabled=true;
                                    row.additionalClass='warning-bar';
                                }
                                row.rowDetailsUrl='';
                                if(row.rowDetailsUrl=='')
                                {
                                    row.rowDetailsUrl='/CRM/ViewQuotationDetails/'+row.Order_id+'/CRM/CRM_QUOTES';
                                }
                            })
                            VueObj.TotalRecords = response.data.data[0].total_records;
                            VueObj.TotalPages = Math.ceil(VueObj.TotalRecords / VueObj.PageSize);
                            VueObj.CurrentPage = VueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $('#DeleteQuotation').addClass('disabled');
                            $('#Sendbulkmail').addClass('disabled');
                          
                        } else {
                            VueObj.noRecord = true;
                            VueObj.QuotationData = [];
                            VueObj.TotalRecords = 0
                        }
                    }else{
                        VueObj.noRecord = true;
                        VueObj.QuotationData = [];
                        VueObj.Headers = [];
                        VueObj.TotalRecords = 0
                    }
                    setTimeout(function(){
                                VueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);
                            VueObj.isLoading = false;
                    
                });
            }
        },
         renderActions: function(row){
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {...actionItem};
                switch (actionItem.title) {
                    case 'View':
                          action.isVisible=row.status_id!='Draft';
                           break;
                        case 'Edit':                               
                            action.isVisible = row.ref_source_id<=0 && row.status_id!='Closed'
                            break;
                        case 'Follow-Ups':
                            action.isVisible = row.status_id != 'Closed';
                            break;
                        case 'Convert Quotation':
                            action.isVisible = row.status_id!='Draft' && row.status_id!='Closed' && row.ref_source_id<=0  && (row.quotation_type=='Deal' || row.quotation_type=='Project')
                            break;                            
                    }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        ConvertToOther: function (item) {
            if (item.quotation_type == 'Deal') {
                var vm = this;
                var msg =vm.$t("ConfirmConvert",[String(vm.$t("Quotation")).toLowerCase(),item.order_number,String(vm.$t("Sales_Order")).toLowerCase()]);
                vm.confirmR(msg, true,vm.$t("ConvertQuotation"), function () {
                    var postJSON = {
                        SubModuleCode: "CRM_QUOTES",
                        OrderId: item.Order_id,
                        ToSubModuleCode: 'CRM_SALES_ORDERS'
                    }
                    var objectPostString = JSON.stringify(postJSON);                     
                    QuoteDataService.ConvertOrder(objectPostString).then(function (response) {                           
                        if (response.data[0].Code == "1") {      
                            msg=vm.$t('SubmitSuccess',[vm.$t('Sales_Order'),vm.$t('Generated'),response.data[0].Name]);                          
                            vm.FetchData();                            
                             vm.ShowAlert(msg, "success", true, vm.$t("Alert"));                            
                        }
                        else {                            
                            vm.ShowAlert(vm.$t('AddedError', [vm.$t('Quotation')]), "failure", true, vm.$t("Alert"));
                        }                         
                    });
                });
            } else if (item.quotation_type == 'Project') {
                this.openDialogueForQuoteToProject(item.Order_id);
            }
        },
        openDialogueForQuoteToProject: function (orderId) {
            this.orderIdForConvertToProject = orderId;
            this.showQuoteToProjectModal = (this.showQuoteToProjectModal) ? false : true;
        },
        closeDialogueForQuoteToProject:function(){
            this.showQuoteToProjectModal =false;
            this.FetchData();
        },
        commonDeleteData: function (event) {
            var vm = this;
            QuoteDataService.CheckPrivilege(vm.privilegeParams + 'DeleteQuotation')
                .then(function (response) {
                    if (response.data) {
                        var val = "";
                        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                            if (val.length > 0)
                                val += ',';
                            val += $(this).val();
                        });
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Quotation")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_QUOTES"
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                QuoteDataService.CommonDelete(objectPostString).then(function (response) {
                                    var box = "";
                                    if (response.status == 200) {
                                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                        $("#DeleteMultiple").addClass("disabled");
                                        box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Quotation')]), "success", true, vm.$t("Alert"));
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        vm.FetchData();
                                    }
                                    else {
                                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Quotation')]), "failure", true, vm.$t("Alert"));
                                    }
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 2000);

                                },);
                            });
                        }
                        else {
                            alert(vm.$t("Selectanyrecordtodelete"));
                        }
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }

    });               
        },
        ManageView: function () {            
          this.ShowManageView = true;
          setTimeout(function () {
              $("#modalManageView").modal('show');
              setTimeout(function () {
                  SetBootstraptoolTip();0
              }, 200);
          }, 1000);           
        },
        pagerMethod: function (value) {              
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        openFilterPopUp(source_id) {
            
            this.cancelOrRefresh();
            this.showFilter = true;
        },
        cancelOrRefresh: function () {
            this.searchCondition = '';
            this.stageName = '';
            this.selectedQuoteType = '',
            this.Status = '';
            this.txtquotenumber = '',
            this.txtquotetitle = '',
            this.quotationNumberId = '';
            this.Title = '';
            this.QuotationType = '';
            this.quotationDateFrom = '';
            this.quotationDateTo = '';
            this.PageNumber = 1;
            this.IsAnySearch = false;
            this.FetchData();
        },
        sortdata: function (s) {
                var SortDir = "sort tb_headerSortDown";
                this.SortBy = s;
                    if (s === this.SortBy) {
                    if (this.SortExp == "") {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                    else if (this.SortExp == "ASC") {
                        this.SortExp = "DESC";
                        SortDir = "sort tb_headerSortUp";
                    }
                    else
                    {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                }               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
            },
            commonHeaderButtonClick: function (event) { 
                switch (event.callbackfunction) {                                     
                    case "ManageView":
                      this.ManageView();
                        break;                                   
                    case "openFilterPopUp":
                        this.openFilterPopUp();
                        break;
                    case "downloadExceldata":
                        this.FetchData(true);
                        break;
                    case "sendBulkEmail":
                    this.SendBulkEmail();
                        break;
                    case "commonDeleteData":
                        this.commonDeleteData();
                        break;  
                    case "removeFilter":
                        var ths = this;
                        this.searchCondition = '';
                        const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';});
                        ths.listheaderbuttons[removeFilterindex].isvisible=false;
                        const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';});
                        ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                         try {
                            ths.appliedFilter = [];
                         } catch (e) { };
                         ths.FetchData();
                        break; 
                        case "setTourGuideSteps":
                        this.setTourGuideSteps(0);
                        break;
                }
        },
        SendBulkEmail: function (event) {
                     var vm=this;
                     QuoteDataService.CheckPrivilege(vm.privilegeParams + 'SendBulkEmail')
                .then(function (response) {
                    if (response.data) {
                        var ids = '';
                        var val = '';
                        $('.chkItems:checkbox:checked').not("[id^='chkAll']").not(".CloseLeadInput").each(function () {
                            if (val.length > 0)
                                val += ',';
                            val += $(this).val();
                            ids = val;
                        });
                        if (ids.length > 0) {
                            vm.showAddEmailTemplate = true;
                            vm.modalItem = ids;
                        }
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }

            });             
            },
        getStatuslistForListing: function (StatusType, lang) {
                var vm = this;
                var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                 
                QuoteDataService.statusdropdown(url).then(function (response) {
                    if (response.data.status == "Success") {
                        var json = response.data.result;
                        vm.StatusListForListing = json.data;
                    }
                });
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        leftCommonSearch: function (value) {        
                this.PageNumber = value.PageNumber;
                if (typeof value.searchCondition != 'undefined') {
                    this.searchCondition = value.searchCondition;
                }
                else {
                    this.searchCondition = "";
                }
                this.FetchData();
        },
        currentPageMethod: function (value) {          
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
            list.push(i);
            }
            this.PageSizeOption = list;
        },
        openAddNewEvent(item, source_id) {
           
            var vm = this;
            vm.souceID = source_id.Order_id;
            vm.selectedItem = item;
            vm.showAddEventModal = (vm.showAddEventModal) ? false : true;                                            
        },
        closeEventViewModal: function () {                       
            this.showAddEventModal = false;
        },
        actionButtonClick:function(callbackfunction,item,index,event){                        
            var vm=this;
            switch(callbackfunction){                
                  case 'checkPrivilegeView':
                     break;
                 case 'checkPrivilegeDetails':
                     break;                    
                 case 'openAddNewEvent':
                     vm.openAddNewEvent(item.Order_id, item);
                     break;  
                  case 'ConvertToOther':
                  vm.ConvertToOther(item);
                  break;        
            }
        }, 
        clickEventOfColumn:function(row, column){
            if(column.COLUMN_NAME == 'order_number'){
                this.$router.push({ path: '/CRM/ViewQuotationDetails/'+row.Order_id+'/CRM/CRM_QUOTES' });
            }
        },
        
    }
   
}
</script>
<template>
<div>
   <loader :is-visible="isLoading"></loader>
 
    <TgList IsShowAction="true"
        ModuleName="CRM" 
        SubModuleCode="CRM_QUOTES" 
        IdentityColumn="Order_id"
        :HeaderText="$t('CRM_QUOTES')"
        :ListData="QuotationData" 
        :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" 
        :TotalRecords="TotalRecords"
        :SearchFields="leftSearchFields"
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"
        :LegendArray="LegendArray"
        :IsFilterApplied="searchCondition?true:false"
        :SortExp="SortExp"
        :SortBy="SortBy"
        :CurrentPageNumber="PageNumber"
        v-on:LeftsearchButtonClick="leftCommonSearch"
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick"
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status :isfor="'CRM_QUOTES'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_QUOTES')" ref="updatestatus" v-bind:dataid="data.row.Order_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index" v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true"
        >
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_QUOTES')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template>
        </template>
    </TgList>
    <searchfilter 
        v-if="showFilter" 
        submodulecode="CRM_QUOTES" 
        modulename="CRM" 
       v-on:close="showFilter = false" 
        v-bind:callbackfunction="FetchData"
        v-on:FilterData="FilterData">
        <h5 slot="header">{{$t('Search')}}</h5>
    </searchfilter> 
    <viewlayout 
        v-if="ShowManageView" 
        v-show="ShowManageView" 
        submodulecode="CRM_QUOTES"
        modulename="CRM"                
        @close="ShowManageView = false"
        v-bind:callbackfunction="FetchData"
        v-bind:commonapiurl="commonAPIUrl"
        :usertype="usertype">
        <h5 slot="header">{{$t('Search')}}</h5>
    </viewlayout>
    <modalemail 
        v-if="showAddEmailTemplate" 
        @close="showAddEmailTemplate = false" 
        :selectedids="modalItem" 
        :userid="userid" 
        :companyid="companyId" 
        submodulecode="CRM_QUOTES">
        <h5 class="modal-title" slot="header">{{$t("SendEmail")}}</h5>
    </modalemail>
    <modalfollowupslist 
        :srcId="souceID"                 
        v-if="showAddEventModal" 
        modulename="CRM" 
        submodulecode="CRM_QUOTES" 
        :userid="userid" 
        v-bind:baseurl="BaseUrlForManageView" 
        @close="closeEventViewModal">
    </modalfollowupslist>
    <modalconvertQuoteToProject v-if="showQuoteToProjectModal" :orderIdForConvertToProject="orderIdForConvertToProject" @close="closeDialogueForQuoteToProject"></modalconvertQuoteToProject>
</div>
</template>




